<template>
    <span @click="num" v-if="!loading">
        同步
    </span>

    <span v-else>
        等待
    </span>

</template>

<script>
// 以 Boos 为单位的递归开头，每一个无上线成员均为一名 Boos
// 有多少 Boos 则有多少个递归运算

import bonus from '../../common/es/bonus/bonus'
import LoadingSmall from '../Data/LoadingSmall.vue'
    export default {
  components: { LoadingSmall },
        props: {
            user_s: {
                type: Array
            },
            user_e: {
                type: Array
            }
        },
        name: '',
        data() {
            return {
                loading: false,
                bonus: [ ]
            }
        },
        mounted() {
            this.getBonus()
        },
        methods: {
            async getBonus() {
                const b = await this.conn.get(
                    this.api.bonus, 
                    this.$store.state.token
                )

                if (b) {
                    this.bonus = b.map(e => {
                        return {
                            id: e.id,
                            user: e.user.id
                        }
                    })
                }
            },
            async netoSave_enroller(user) {
                const data = { usd_recommend: user.bonus.usd_recommend }
                this.bonus.map(e => {
                    if (e.user == user.id) {
                        data.bonus = e.id
                    }
                })
                const res = await this.conn.put(
                    this.api.bonus + '/' + data.bonus,
                    this.$store.state.token,
                    data
                )
            },
            async netoSave_sponser(user) {
                const data = {
                    ...user.bonus, vip: user.vip.type + '_' + user.vip.star
                }
                this.bonus.map(e => {
                    if (e.user == user.id) { data.bonus = e.id }
                })
                const res = await this.conn.put(
                    this.api.bonus + '/' + data.bonus,
                    this.$store.state.token,
                    data
                )
            },
            num() {
                this.loading = true

                this.user_s.map(e => {

                    bonus.bonus(e, this.netoSave_sponser)
                    return e
                })

                this.user_e.map(e => {
                    bonus.bonus(e, this.netoSave_enroller, 'usd_recommend')
                })

                setTimeout(() => {
                    this.loading = false

                    this.$emit('finished_Father')
                }, 600)
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


